@import url("https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@0,100;0,700;1,400;1,700&display=swap");
#bg-video {
  position: fixed;
  overflow: hidden;
  height: auto;
  width: 120%;
  height: 120%;
  top: 0;
  left: 0;
  z-index: -2;
  margin-top: -7.5%;
}

#video-overlay {
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: rgba(45, 51, 61, 0.5);
  background-image: url("./gridiron.png");
  background-size: cover;
}

#menu {
  width: 93%;
  height: 100vh;
  overflow-y: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}

.blackscreen {
  position: absolute;
  z-index: 2;
  background-color: #2b2b2b;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  opacity: 0;
}

.menu__slot {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-preferred-size: 28%;
      flex-basis: 28%;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.menu__slot:hover {
  background-color: whitesmoke;
}

.menu__slot:nth-child(1):hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#c5f9ba), color-stop(#fdc3cf), color-stop(#ecd2fe), to(#bddaf8));
  background-image: linear-gradient(90deg, #c5f9ba, #fdc3cf, #ecd2fe, #bddaf8);
  background-size: 300% 300%;
  -webkit-animation: gradient 10s ease infinite;
          animation: gradient 10s ease infinite;
}

.menu__slot:nth-child(2):hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#ecd2fe), color-stop(#bddaf8), color-stop(#c5f9ba), to(#fdc3cf));
  background-image: linear-gradient(90deg, #ecd2fe, #bddaf8, #c5f9ba, #fdc3cf);
  background-size: 300% 300%;
  -webkit-animation: gradient 7.5s ease infinite;
          animation: gradient 7.5s ease infinite;
}

.menu__slot:nth-child(3):hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#c5f9ba), color-stop(#bddaf8), color-stop(#ecd2fe), to(#fdc3cf));
