.menu__slot:nth-child(4):hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#fdc3cf), color-stop(#c5f9ba), color-stop(#ecd2fe), to(#bddaf8));
  background-image: linear-gradient(90deg, #fdc3cf, #c5f9ba, #ecd2fe, #bddaf8);
