.menu__slot:nth-child(5):hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#bddaf8), color-stop(#fdc3cf), color-stop(#c5f9ba), to(#ecd2fe));
  background-image: linear-gradient(90deg, #bddaf8, #fdc3cf, #c5f9ba, #ecd2fe);
