.menu__slot:hover,
.menu__slot:hover div[class^="slot-item-"] {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 0;
  padding-top: 18vh;
  -ms-flex-preferred-size: 37vw;
      flex-basis: 37vw;
  justify-content: center;
  -webkit-transition: -webkit-flex-basis 100ms ease-in-out;
