  transition: flex-basis 100ms ease-in-out, -webkit-flex-basis 100ms ease-in-out, -ms-flex-preferred-size 100ms ease-in-out;
}

.menu__slot:hover div.char {
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  -webkit-transition: -webkit-transform 10s ease-in;
  transition: -webkit-transform 10s ease-in;
  transition: transform 10s ease-in;
  transition: transform 10s ease-in, -webkit-transform 10s ease-in;
}

