.char {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 0;
}

.menu__slot h3 {
  text-transform: uppercase;
  position: relative;
  text-align: center;
  color: #bbbbbb;
  background-color: rgba(45, 51, 61, 0.8);
  padding: 3.6% 5%;
  font-size: 2em;
  font-weight: 300;
  z-index: 1;
  background-image: url("./led-grid.png");
  background-size: cover;
  font-family: 'Ubuntu Mono', monospace;
}

.menu__slot:hover > div > h3 {
  font-size: 2.45em;
  font-weight: bold;
  min-width: 80%;
  max-width: 80%;
  text-align: left;
  color: #f4f4f4;
  font-family: 'Ubuntu Mono', monospace;
}

.menu__slot:active > div > h3 {
  color: #2d2e2d;
  -webkit-transition: 0.25s ease;
  transition: 0.25s ease;
}

.menu__slot p {
  opacity: 0;
  position: relative;
  z-index: 1;
  line-height: 1.5em;
  color: white;
  font-weight: 500;
  font-size: 110%;
  text-shadow: 0 2px 5px black;
}

.menu__slot:hover > div > p {
  opacity: 1;
  padding: 0 8% 0 8%;
}

@-webkit-keyframes fadeOut {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
